import SignIn from "./signIn";
import axios from "axios";
import {useCallback, useEffect, useState} from "react";
import '../../css/mypage.css'

export default function Mypage() {

    // TODO   Y  update rent_delay     

    /*   useState   */
    const [memberView, setMemberView] = useState([]) //  
    const [currentMemberPw, setCurrentMemberPw] = useState("") //  
    const [changeMemberPw, setChangeMemberPw] = useState(""); //  
    const [memberNickname, setMemberNickname] = useState(""); //  
    const [memberBirth, setMemberBrith] = useState(""); //  
    const [memberAdd, setMemberAdd] = useState("") //   
    const [memberPostcode, setMemberPostcode] = useState("") //  
    const [memberDetailAdd, setMemberDetailAdd] = useState("") //  
    const [showBook, setShowBook] = useState(false); //    on/off
    const [showBookRentalList, setShowBookRentalList] = useState([]) //    
    const [bookNo, setBookNo] = useState(0); //  
    const [rentNo, setRentNo] = useState(0); //  
    const [prevProfile, setPrevProfile] = useState(null); //  ()
    const [profile, setProfile] = useState(null); //  ( )

    /*   */
    const [currentPwErrorMessage, setCurrentPwErrorMessage] = useState(""); //    
    const [pwErrorMessage, setPwErrorMessage] = useState(""); //   
    const [nicknameErrorMessage, setNicknameErrorMessage] = useState(""); //   
    const [duplicatedNickname, setDuplicatedNickname] = useState(true); //    
    //   
    const currentPasswordOnChangeHandler = useCallback((e) => {
        setCurrentMemberPw(e.target.value);
    }, [])

    //   
    const passwordOnChangeHandler = useCallback((e) => {
        setChangeMemberPw(e.target.value);
    }, [])

    //  
    const memberNicknameOnChangeHandler = useCallback((e) => {
        setMemberNickname(e.target.value);
    }, [])

    //  
    const memberBirthOnChangeHandler = useCallback((e) => {
        setMemberBrith(e.target.value);
    }, [])

    //  
    const memberPostcodeOnChangeHandler = useCallback((e) => {
        setMemberPostcode(e.target.value);
    }, [])

    //  
    const memberAddOnChangeHandler = useCallback((e) => {
        setMemberAdd(e.target.value);
    }, [])

    //  
    const memberDetailAddOnChangeHandler = useCallback((e) => {
        setMemberDetailAdd(e.target.value);
    }, [])

    //   ()
    const profileOnChangeHandler = useCallback((e) => {
        const file = e.target.files[0];
        const imageUrl = URL.createObjectURL(file);
        setProfile(file); //   
        setPrevProfile(imageUrl); //   
    }, [])

    const memberData = sessionStorage.getItem("member"); //    
    const memberObj = JSON.parse(memberData); //  JSON  

    //    
    const getData = async () => {
        const postData = {
            memberId: memberObj.memberId //    
        }

        try {
            const res = await axios.post("/searchMemberInfo", postData, {
                headers: {
                    "Content-Type": "application/json"
                }
            });
            setMemberView(res.data) //      
            setMemberNickname(res.data.memberNickname) // 
            setMemberPostcode(res.data.memberPostcode)// 
            setMemberAdd(res.data.memberAdd)// 
            setMemberDetailAdd(res.data.memberDetailAdd)// 
            setMemberBrith(res.data.memberBirth)// 
        } catch (error) {
            console.error("Error fetching data:", error);
        }

    };

    //     
    const getBookData = async () => {
        const bookData = {
            memberId: memberObj.memberId //    
        }

        try {
            const resData = await axios.post("/showBookRentalList", bookData, {
                headers: {
                    "Content-Type": "application/json"
                }
            })
            setShowBookRentalList(resData.data)//     
        } catch (error) {
            console.error("Error fetching data:", error);
        }
    };

    //   
    const getProfile = async () => {
        const res = await axios.get("/getProfile", {
            params: {
                memberId: memberView.memberId,
            }
        })
        setPrevProfile(res.data);
    }

    useEffect(() => {
        getProfile();
    }, [memberView])

    //     
    useEffect(() => {
        if (JSON.parse(sessionStorage.getItem("bookRent"))) {
            setShowBook(true);
            sessionStorage.removeItem("bookRent");
        }
        getData() //    
        getBookData() //     
    }, []);

    //  
    const bookRentReturn = async () => {

        // update    
        const rentData = {
            bookNo: bookNo, // 
            rentNo: rentNo // 
        }

        try {
            await axios.post("/rentReturn", rentData, {
                headers: {
                    "Content-Type": "application/json"
                }
            })

            getBookData(); //      
        } catch (error) {
            console.error("Error fetching data:", error);
        }
    }

    useEffect(() => {
        bookRentReturn() //   rentNo   
    }, [rentNo]) //      

    const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,}$/;

    //         
    const checkMemberPw = () => {
        //       
        //       
        if (changeMemberPw != "") {
            if (!passwordRegex.test(changeMemberPw) || !changeMemberPw) {
                setPwErrorMessage("  1  8   ");
                //   
            } else {
                setPwErrorMessage("");
            }
        } else {
            setPwErrorMessage("")
        }
    }

    //   
    const checkCurrentMemberPw = () => {
        //       
        //       
        if (currentMemberPw != "") { //   "" 
            if (currentMemberPw != memberView.memberPw) { //    
                setCurrentPwErrorMessage("  ."); //   onblur 
                //   
            } else {
                setCurrentPwErrorMessage(""); //   onblur 
            }
        } else {
            setCurrentPwErrorMessage(""); //     
        }
    }

    //    
    const passwordChange = async () => {

        //      ""      
        if (currentMemberPw == memberView.memberPw && changeMemberPw != "" && pwErrorMessage == "") {
            const passChange = {
                memberId: memberView.memberId, //   
                memberPw: changeMemberPw //   
            }
            try {
                const res = await axios.post("/passwordChange", passChange, {
                    headers: {
                        "Content-Type": "application/json"
                    }
                });
                setCurrentMemberPw(""); //   
                setChangeMemberPw(""); //    
                alert(" .")
                getData();
            } catch (error) {
                console.error("Error fetching data:", error);
            }
        }
    }

    // API  ,   
    const handlePostcode = () => {
        new window.daum.Postcode({
            oncomplete: function (data) {
                var addr = ''; //  
                // var extraAddr = ''; //  

                if (data.userSelectedType === 'R') {
                    addr = data.roadAddress;
                } else {
                    addr = data.jibunAddress;
                }

                document.getElementById('memberPostcode').value = data.zonecode;
                document.getElementById("memberAdd").value = addr;
                document.getElementById("memberDetailAdd").focus();
                setMemberPostcode(data.zonecode);
                setMemberAdd(addr);
                setMemberDetailAdd("")
            }
        }).open();
    };

    // TODO
    //   

    // save   
    const saveMemberData = async () => {
        if (memberNickname.length == 0) {
            alert("  .")
            return;
        } else if (memberNickname.length < 2) {
            alert(" 2   .")
            return;
        } else if (memberNickname == '' || memberNickname == 'admin') {
            alert("     .")
            return;
        }
        if (duplicatedNickname) { // true 
            alert("    .")
            return
        }
        const memberData = {
            memberId: memberView.memberId, //   
            memberNickname: memberNickname, // ,  
            memberPostcode: memberPostcode, // 
            memberAdd: memberAdd, // 
            memberDetailAdd: memberDetailAdd, // 
            memberBirth: memberBirth // 
        }
        try {
            await axios.put("/changeMemberData", memberData, {
                headers: {
                    "Content-Type": "application/json"
                }
            });

            //     
            // TODO
            //    sessionStorage       
            //    data  sessionStorage  
            if (profile) {
                const imageForm = new FormData();
                imageForm.append('profile', profile);
                imageForm.append('memberId', memberView.memberId);
                axios.post('/changeMemberProfile', imageForm, {
                    headers: {
                        "Content-Type": "multipart/form-data",
                    },
                }).then(response => (console.log("  : ", response.data)))
            }
            alert(" .")
            getData();
        } catch (error) {
            console.error(" ! => ", error)
        }
    }

    //   onblur   
    const nicknameDupCheck = async () => {

        if (memberNickname.length < 2) {
            setNicknameErrorMessage(" 2   .");//  onblur
            return;
        } else if (memberNickname == '' || memberNickname == 'admin') {
            setNicknameErrorMessage("     ."); //  onblur
            return;
        }

        try {
            const res = await axios.post("/checkNick", {
                memberNickname: memberNickname // 
            });

            if (memberNickname != memberView.memberNickname) { //     
                //    
                if (res.data != 0) {
                    //   
                    setDuplicatedNickname(true); // 
                    setNicknameErrorMessage(" .");
                    return
                } else {
                    setDuplicatedNickname(false);// 
                    setNicknameErrorMessage("");
                }
            }
            setDuplicatedNickname(false); //   
        } catch (error) {
            console.error(" ! ", error);
        }

    }

    const [activeTab, setActiveTab] = useState('tab1');

    return (
        <main className="rundry">
            <section className="i pg fh rm ki xn vq gj qp gr hj rp hr rundry">
                <div className="bb ze ki xn 2xl:ud-px-0">
                    <body>
                    <section id="content" className="container">
                        <div className="page-heading">
                            <div className="media clearfix">
                                <div className="media-left pr30">
                                    <a href="#">
                                        <div className="mt-2">
                                            {prevProfile
                                                ?
                                                <img src={prevProfile}/>
                                                :
                                                <img src="/images/profile/basic_profile.png"/>
                                            }

                                        </div>
                                    </a>
                                </div>
                                <div className="media-body va-m">
                                    <h2 className="media-heading">OOO </h2>
                                </div>
                            </div>
                        </div>


                        <div className="row">
                            <div className="tab-block">
                                <ul className="nav nav-tabs">
                                    <li className="active">
                                        <a
                                            data-toggle="tab"
                                            className={` ${activeTab === 'tab1' ? 'active' : ''}`}
                                            onClick={() => setActiveTab('tab1')}
                                        >
                                              
                                        </a>
                                    </li>
                                    <li>
                                        <a
                                            data-toggle="tab"
                                            className={`${activeTab === 'tab2' ? 'active' : ''}`}
                                            onClick={() => setActiveTab('tab2')}
                                        >
                                               
                                        </a>

                                    </li>
                                    <li>
                                        <a
                                            data-toggle="tab"
                                            className={` ${activeTab === 'tab3' ? 'active' : ''}`}
                                            onClick={() => setActiveTab('tab3')}
                                        >
                                            
                                        </a>

                                    </li>
                                </ul>


                                <br />
                                <div className="tab-content p30" style={{color: "black"}}>
                                    {activeTab === 'tab1' && (
                                        <div id="tab1" className="tab-pane active">
                                            <div className="media mt25">
                                                <div
                                                    className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6 border-t border-gray-900/10 pt-12">

                                                    <div className="sm:col-span-2 border-gray-900/10">
                                                        <label htmlFor="memberName"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                            
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                readOnly={true}
                                                                type="text"
                                                                name="memberName"
                                                                value={memberView.memberName}
                                                                id="memberName"
                                                                autoComplete="given-name"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>
                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="memberNickname"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                            
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                name="memberNickname"
                                                                onChange={memberNicknameOnChangeHandler}
                                                                onBlur={nicknameDupCheck}
                                                                value={memberNickname}
                                                                id="memberNickname"
                                                                autoComplete="family-name"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                        <p id="nicknameErrorMessage"
                                                           className="text-red">{nicknameErrorMessage}</p>
                                                    </div>

                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="memberId"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                            
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                readOnly={true}
                                                                type="text"
                                                                name="memberId"
                                                                id="memberId"
                                                                value={memberView.memberId}
                                                                autoComplete="given-name"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>


                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="memberPw"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                             
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                id="memberPw"
                                                                name="memberPw"
                                                                type="password"
                                                                onChange={currentPasswordOnChangeHandler}
                                                                onBlur={checkCurrentMemberPw}
                                                                value={currentMemberPw}
                                                                autoComplete="memberPw"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                            <p id="pwErrorMessage"
                                                               className="text-red">{currentPwErrorMessage}</p>
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="memberNewPw"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                             
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                id="memberNewPw"
                                                                name="memberNewPw"
                                                                type="password"
                                                                autoComplete="memberNewPw"
                                                                onChange={passwordOnChangeHandler}
                                                                value={changeMemberPw}
                                                                onBlur={checkMemberPw}
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                            <p id="pwErrorMessage"
                                                               className="text-red">{pwErrorMessage}</p>
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-2 mt-8 text-xl">
                                                        <button
                                                            onClick={passwordChange}
                                                            className="btn btn-dark"
                                                        >
                                                             
                                                        </button>
                                                    </div>


                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="memberTel"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                            
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                readOnly={true}
                                                                id="memberTel"
                                                                name="memberTel"
                                                                value={memberView.memberTel}
                                                                type="text"
                                                                autoComplete="memberTel"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                        <label htmlFor="memberTel"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                             
                                                        </label>
                                                        <div className="mt-2">
                                                            {prevProfile
                                                                ?
                                                                <img src={prevProfile}/>
                                                                :
                                                                <img src="/images/profile/basic_profile.png"/>
                                                            }
                                                            <input
                                                                name="memberProfile"
                                                                accept='image/jpg, image/png, image/jpeg'
                                                                type="file"
                                                                onChange={profileOnChangeHandler}
                                                                className="block rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                            <button onClick={() => setProfile(null)}> </button>
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-2 sm:col-start-1">
                                                        <label htmlFor="memberPostcode"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                            
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                readOnly={true}
                                                                type="text"
                                                                name="memberPostcode"
                                                                id="memberPostcode"
                                                                onChange={memberPostcodeOnChangeHandler}
                                                                value={memberPostcode}
                                                                autoComplete="memberPostcode"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>


                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="memberAdd"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                             
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                readOnly={true}
                                                                type="text"
                                                                name="memberAdd"
                                                                id="memberAdd"
                                                                onChange={memberAddOnChangeHandler}
                                                                value={memberAdd}
                                                                autoComplete="memberAdd"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>
                                                    <div className="sm:col-span-1">
                                                        <label htmlFor="memberDetailAdd"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                            
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                name="memberDetailAdd"
                                                                value={memberDetailAdd}
                                                                id="memberDetailAdd"
                                                                onChange={memberDetailAddOnChangeHandler}
                                                                autoComplete="memberDetailAdd"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>
                                                    <div className="sm:col-span-1">
                                                        <div className="mt-2">
                                                            <button
                                                                onClick={handlePostcode}
                                                                className="mt-5 bg-blue-500 rounded-xl ms-2 text-white h-12 w-28 font-bold"
                                                            >
                                                            </button>
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="memberSub"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                              
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                readOnly={true}
                                                                type="text"
                                                                name="memberSub"
                                                                id="memberSub"
                                                                value={memberView.memberSub}
                                                                autoComplete="memberSub"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="memberBirth"
                                                               className="block text-lg font-medium leading-6 text-gray-900">
                                                            
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="date"
                                                                name="memberBirth"
                                                                id="memberBirth"
                                                                value={memberBirth}
                                                                onChange={memberBirthOnChangeHandler}
                                                                autoComplete="memberBirth"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-lg sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>)}
                                    {activeTab === 'tab2' && (
                                        <div id="tab2" className="tab-pane">
                                            <div className="media mt25">
                                                {!showBook ?
                                                    (
                                                        <div className="mt-6 flex items-center justify-end gap-x-6">

                                                        <button
                                                            onClick={saveMemberData}
                                                            className="btn btn-dark"
                                                        >
                                                            
                                                        </button>
                                                    </div>) :null}
                                            </div>
                                        </div>)}
                                    {activeTab === 'tab3' &&
                                        (
                                            <div id="tab3" className="tab-pane">
                                                <div className="media mt25">
                                                    <h1>3</h1>
                                                </div>
                                            </div>
                                        )
                                    }
                                </div>


                            </div>
                        </div>
                    </section>
                    </body>

                </div>
            </section>
        </main>
    )
}
